.navbar-box {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  padding: .4rem 0 .4rem 9.3rem;
  overflow-x: hidden;
  font-size: 0;
}

.navbar-logo {
  position: absolute;
  left: 3.7rem;
  top: 50%;
  transform: translateY(-50%);
  width: 1.8rem;
  font-size: 0;
}

.navbar-list {
  display: inline-block;
  font-size: .15rem;
  color: #fff;
  white-space: nowrap;
}
.navbar-item {
  display: inline-block;
  margin-right: 1.26rem;
  padding: .1rem 0;
}
.navbar-item.active {
  border-bottom: 3px solid #fe9f3a;
}