.msg-input-box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
  padding: .44rem .55rem;
  background: rgba(11, 161, 248, 0.1);
  border: .02rem solid #1a82d2;
}
.msg-title {
  padding-bottom: .2rem;
  text-align: center;
  font-size: .25rem;
  color: #00e1eb;
}
.msg-input-item {
  box-sizing: border-box;
  position: relative;
  display: block;
  outline: none;
  width: 5.5rem;
  height: .6rem;
  line-height: .6rem;
  margin-top: .2rem;
  padding: 0 .3rem;
  border: .02rem solid #1a82d2;
  background: transparent;
  color: #fff;
}
.msg-input-item::-webkit-input-placeholder {
  color: #bbcfe8;
}
.msg-btn {
  margin-top: .44rem;
  padding: .14rem 0;
  font-size: .32rem;
  text-align: center;
  background: #00e1eb;
  color: #000;
}
.msg-select-item {
  box-sizing: border-box;
  position: relative;
  display: block;
  width: 5.5rem;
  height: .6rem;
  line-height: .6rem;
  margin-top: .2rem;
  padding: 0 .3rem;
  border: .02rem solid #1a82d2;
  background: transparent;
  color: #bbcfe8;
  font-size: .12rem;
  user-select: none;
}
.msg-select-icon {
  position: absolute;
  top: 60%;
  right: .21rem;
  transform: translateY(-50%);
  border-width: .13rem;
  border-color: #00e1eb transparent transparent;
  border-style: solid;
}
.select-box {
  position: absolute;
  top: 4.3rem;
  background: #fff;
  z-index: 100;
}
.select-item {
  box-sizing: border-box;
  position: relative;
  display: block;
  width: 5.5rem;
  height: .6rem;
  line-height: .6rem;
  margin-top: .2rem;
  padding: 0 .3rem;
  color: #000;
  font-size: .12rem;
  user-select: none;
}
.toast {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
  padding: 0 30px;
  line-height: 50px;
  height: 50px;
  font-size: 15px;
  border-radius: 25px;
  background: rgba(0, 0, 0, .6);
  color: #fff;
}